*{
  font-family: Georgia, 'Times New Roman', Times, serif;
}
body{
  padding:60px;

}

input{
  color: red;
  width:100%;
  font-size:1.3rem;
}

.card{
  align-content: center;
  border: 1px solid red;
  border-radius: 20px;
  margin:10px;
  overflow: hidden;
}

.card p{
  padding:5px;
  text-align:center;
}

.card img{
  max-width: 170px;
}

.card-grid{
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
}